.projects-grid{
    width:100%;
    box-sizing: border-box;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    overflow-y: scroll;
    padding-top: 100px;
    scroll-behavior: smooth;
}

.project-container{
    width:400px;
    height:350px;
    border: 1px solid rgba(255,255,255,0.8);
    position:relative;
    margin:5px;
    animation: projectContainerStartAnimation 1s ease-in-out forwards;
    transition: all 0.5s ease-in-out;
}

.project-container:not(.project-container-active){
    cursor:pointer;
}

.project-container:not(.project-container-active):hover{    
    -webkit-box-shadow: 0px 0px 15px 0px rgba(255,255,255,0.75);
    -moz-box-shadow: 0px 0px 15px 0px rgba(255,255,255,0.75);
    box-shadow: 0px 0px 15px 0px rgba(255,255,255,0.75);
}

@keyframes projectContainerStartAnimation{
    to{opacity:1;}
}

.project-img-container {
    position:relative;
    width:100%;
    height:100%;
    border-radius: 0;
    overflow:hidden;
    -webkit-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
}

.project-img-container .img{
    position: absolute;
    height: 100%;
    top: 50%;
    left: 50%;
    margin: 0 auto;
    transform: translate(-50%, -50%);
    transition: all 0.5s ease-in-out;
}

.project-container:hover:not(.project-container-active) .project-img-container .img {
    top:0;
    left:0;
    transform: translate(-15%, -15%) scale(1.5);
    transition: all 0.5s ease-in-out;
}

.project-container-active{
    height:500px;
    width:100%;
    transition: all 0.5s ease-in-out;
}

.project-container-active .project-img-container .img{
    /* transform:  scale(1); */
    /* top:0;
    left:0; */
}

.project-text-container{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    /* overflow-y:scroll; */
    color: #fff;
    background-color: rgba(0,0,0,0.5);
    transition: all 0.5s ease-in-out;
}

.project-container-active .project-text-container,
.project-container:hover .project-text-container {
    background-color: rgba(0,0,0,0.9);
}

.project-container .top-text {
    display:flex;
    flex:1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.project-container-active .top-text {
    height:350px;
}

.bottom-text {
    height: 150px;
    opacity:0;
    overflow-y: scroll;
    scroll-behavior: smooth;
    animation:rightTextStartAnimation 1s ease-in-out forwards;
}

@keyframes rightTextStartAnimation {
    from{}
    to{opacity:1}
}

.project-links{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.project-links .link{
    width:50px;
    height:50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid rgba(255,255,255,0.3);
    opacity:0.8;
    margin:5px;
    cursor:pointer;
}

.project-container-active .project-links .link,
.project-container:hover .project-links .link{
    transform: rotateY(360deg);
    transition: all 0.5s ease-in-out;
}

.project-links .link:hover{
    opacity:1 !important;
    border: 1px solid rgba(255,255,255,0.5);
     /* border: 1px solid rgba(255,255,255,0.5); */
    -webkit-box-shadow: 0px 0px 25px 0px rgba(255,255,255,0.3);
    -moz-box-shadow: 0px 0px 25px 0px rgba(255,255,255,0.3);
    box-shadow: 0px 0px 25px 0px rgba(255,255,255,0.3);
    transition: all 0.5s ease-in-out;
}

.project-container-active .close-button{
    position: absolute;
    top:5px;
    right:5px;
    background-color: transparent;
    text-decoration: none;
    border:none;
    cursor:pointer;
}

.project-container .more-button{
    position: absolute;
    bottom:5px;
    right:5px;
    background-color: transparent;
    text-decoration: none;
    border:none;
    cursor:pointer;
}

.bottom-text::-webkit-scrollbar,
.project-text-container::-webkit-scrollbar,
.projects-grid::-webkit-scrollbar {
    width: 1px;     /* Tamaño del scroll en vertical */
    height: 1px;    /* Tamaño del scroll en horizontal */
}

/* Ponemos un color de fondo y redondeamos las esquinas del thumb */
.bottom-text::-webkit-scrollbar-thumb,
.project-text-container::-webkit-scrollbar-thumb,
.projects-grid::-webkit-scrollbar-thumb {
    background: #fff;
}

/* Cambiamos el fondo y agregamos una sombra cuando esté en hover */
.bottom-text::-webkit-scrollbar-thumb:hover,
.project-text-container::-webkit-scrollbar-thumb:hover,
.projects-grid::-webkit-scrollbar-thumb:hover {
    background: #fff;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Cambiamos el fondo cuando esté en active */
.bottom-text::-webkit-scrollbar-thumb:active,
.project-text-container::-webkit-scrollbar-thumb:active,
.projects-grid::-webkit-scrollbar-thumb:active {
    background: rgba(0,0,0,0.6);
}

/* Ponemos un color de fondo y redondeamos las esquinas del track */
.bottom-text::-webkit-scrollbar-track,
.project-text-container::-webkit-scrollbar-track,
.projects-grid::-webkit-scrollbar-track {
    background: rgba(0,0,0,0.3);
    border-radius: 4px;
}

/* Cambiamos el fondo cuando esté en active o hover */
.bottom-text::-webkit-scrollbar-track:hover,
.bottom-text::-webkit-scrollbar-track:active,
.project-text-container::-webkit-scrollbar-track:hover,
.project-text-container::-webkit-scrollbar-track:active,
.projects-grid::-webkit-scrollbar-track:hover,
.projects-grid::-webkit-scrollbar-track:active{
    background: rgba(0,0,0,0.4);
}