.contact-div{
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items:center;
  padding: 5px 5px;
  margin: 10px 0;
  color:#fff;
  cursor:pointer;
  border: 1px solid rgba(255,255,255,0);
  opacity:0;
  transition: all 0.5s;
  animation: contactDivStartAnimation 1s ease-in-out forwards;
}

@keyframes contactDivStartAnimation {
  100% { opacity:1;}
}

.contact-div .left-component{
  margin:0 10px;
  opacity:0;  
  transform: translateX(-100%);
  animation: leftComponentAnimation 0.7s ease-in-out forwards;
}

.contact-div .right-component{
  margin:0 5px;
  margin-top:5px;
  opacity:0;
  transform: translateX(100%);
  animation: rightComponentAnimation 0.7s ease-in-out forwards;
}

@keyframes leftComponentAnimation {
  0% {transform: translateX(-100%);}
  100% { transform: translateX(0) rotateY(360deg);  opacity:1;}
}

@keyframes rightComponentAnimation {
  0% {transform: translateX(100%);}
  100% { transform: translateX(0) rotateX(360deg);  opacity:1;}
}

.contact-div:hover{
  border: 1px solid rgba(255,255,255,0.8);
}

.contact-div .left-component .contact-icon{
  width:30px;
  height:30px;
}
