.container{
    position:absolute;
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    transform: translateY(100%);
    transition: transform 0.3s ease-in-out;
}

.container-show{
  transform: translateY(0);
}

.container-about{
  background: rgba(0,0,0,0.8);
}

.container-projects{
  background: #468E8F;
  background: -webkit-linear-gradient(to top, #468E8F, #000);
  background: linear-gradient(to top, #468E8F, #000);
}

.container-contact{
  background: #B2D7B5;
  background: -webkit-linear-gradient(to top, #B2D7B5, #000);
  background: linear-gradient(to top, #B2D7B5, #000);
}